import React, {useState, useEffect} from 'react';
import axios from 'axios';
import './Profile.css'
import "../API/api"
import moment from "moment";

const AssignProject = () => {
  const [assignproject, setAssignproject] = useState({
    fullName:[''],
    projectName:'',
    onboarddate:"",
  })
  const[project, setProject] = useState([]);
  const[techemployee, setTechemployee] = useState([]);
  const[assigndata, setAssigndata] = useState([]);
   
  const heading = ['Employee Name', 'Project Name', 'Client Name', 'Start-Date', 'Status'];

  const {fullName, projectName, onboarddate} = assignproject

 const oninputChange =(e)=>{
  setAssignproject({
      ...assignproject,
      [e.target.name]:[e.target.value]
    })
 }
 
 const assignProjectData = (e)=>{
  e.preventDefault();
    axios.post(`${global.API_URL}/smg/assignedProject/saveAssignedProject`, {
      employeeNameList: fullName,
      projectName: `${projectName}`,
      onBoardDate: `${onboarddate}`
    }).then((res) => {
      console.log("successfully assigned", res.data);

      if (res.data.msg == "assigned") {
        console.log("assigned", res.data)
        alert("Project Assigned!")
        window.location.reload(false);
      }
    }).catch((err) => {
      console.log("some error here", err);
    })
 }  

  useEffect(() => {
    const GetProjectDetails = () => {
      axios.get(`${global.API_URL}/smg/project/getAllProjectDetails`).then((res) => {
        const data = res.data
        console.log("Fetch successfully", data);
        let result = data.filter(items=>items.projectStatus !== "Completed")
        setProject(result);
      }).catch((err) => {
        console.log("some error", err); 
      })
    }
    
    const GetEmpDetails = () => {
      axios.get(`${global.API_URL}/smg/employee/getTechEmp`).then((res) => {
        console.log("Fetch successfully", res.data);
        setTechemployee(res.data);
      
      }).catch((err) => {
        console.log("some error", err); 
      })
    }

    
    
   GetProjectDetails();
   GetEmpDetails();
   GetAssignData();
  }, []);

  const GetAssignData = () => {
    axios.get(`${global.API_URL}/smg/assignedProject/getListOfAssignedProject`).then((res) => {
      console.log("Fetch successfully", res.data);
      setAssigndata(res.data);
    }).catch((err) => {
      console.log("some error", err); 
    })
  }


  const onDeleteAssign = (EmployeeProjectId) => {
    axios.post(`${global.API_URL}/smg/assignedProject/deleteEmployeeProject/${EmployeeProjectId}`)
      .then(() => {
        GetAssignData();
      })
  }



  return (
    <div >
      <form onSubmit={assignProjectData} >
        <h5>Assign Project</h5>
        <div className='formWrapper'>
         <div>
          <label className='form-label'>Employee Name <sup className='star'>*</sup></label>
          <select className='form-select' name="fullName" value={fullName} onChange={oninputChange}>
            <option>Select</option>
            {
              techemployee.map((element)=>{
                return <option key={element.empId}>{element.fullName}</option>
              })
            }
          </select>
         </div>
         <div>
          <label className='form-label'>Project Name</label>
          <select className='form-select' name="projectName" value={projectName} onChange={oninputChange}>
            <option>Select</option>
            {
              project.map((items)=>{
                return <option key={items.projectId}>{items.projectName}</option>
              })
            }
          </select>
         </div>
         <div>
          <label className='form-label'>Onboarding Date</label>
          <input type='date' className='form-control' value={onboarddate} name="onboarddate" onChange={oninputChange}/>
         </div>
         <button type='submit' className='btn btn-success Btn'>Assign Project</button>
          <button type='submit' className='btn btn-warning Btn'>Reset</button>
        </div>
      </form>
      <hr />
      <div className='Border'>
        <table className="table table-striped">
          <thead>
            <tr>
              {
                heading.map((data, index) => (

                  <td key={index} style={{ fontWeight: '600', color: '#252F40' }}>{data}</td>

                ))
              }
            </tr>

          </thead>
          <tbody >
            {
              assigndata.map((data) => {
                return <tr key={data.EmployeeProjectId}  >
                  <td style={{ color: '#2562AB', cursor: 'pointer' }} >{data.EmployeeName}</td>
                  <td >{data.projectName}</td>
                  <td >{data.clientName}</td>
                  <td >{moment(data.onBoardDate).utc().format('MMM Do, YYYY')}</td>
                  <>{data.status ? <td style={{ color: "green" }}> Active </td> : <td style={{ color: "red" }}>Inactive</td>}</>
                  <td className='bi bi-trash3-fill' style={{ color: 'red' }} onClick={()=> onDeleteAssign(data.EmployeeProjectId)} ></td>
                </tr>
              })
            }
          </tbody>
        </table>
      </div>
    </div>
  );
}

export default AssignProject;
